"use client";

import { useRouter } from 'next/router';

const PokemonDetails = () => {
  const router = useRouter();
  const { id } = router.query;

  // Vérifier si les données du Pokémon sont passées correctement
  if (!router.query.pokemon) {
    return <p>Loading...</p>;
  }

  // Récupérer les données du Pokémon depuis les paramètres d'URL
  const pokemon = JSON.parse(router.query.pokemon);

  return (
    <div className="max-w-2xl mx-auto p-6 bg-white shadow-md rounded-lg mt-10">
      <button
        onClick={() => router.push('/')}
        className="mb-4 px-4 py-2 bg-blue-500 text-white rounded-md hover:bg-blue-600"
      >
        ← Retour
      </button>
      <h1 className="text-2xl font-bold text-center mb-4">{pokemon.name}</h1>
      <img
        src={pokemon.image}
        alt={pokemon.name}
        className="w-64 h-64 mx-auto mb-4"
      />
      <div className="text-center">
        <h2 className="text-xl font-semibold">Stats</h2>
        <ul className="list-disc list-inside">
          <li>HP: {pokemon.stats.HP}</li>
          <li>Attack: {pokemon.stats.attack}</li>
          <li>Defense: {pokemon.stats.defense}</li>
          <li>Speed: {pokemon.stats.speed}</li>
          <li>Special Attack: {pokemon.stats.specialAttack}</li>
          <li>Special Defense: {pokemon.stats.specialDefense}</li>
        </ul>
      </div>
      <div className="mt-6">
        <h2 className="text-xl font-semibold text-center mb-2">Évolutions</h2>
        {pokemon.evolutions.length > 0 ? (
          <div className="flex flex-wrap justify-center gap-4">
            {pokemon.evolutions.map((evolution) => (
              <div key={evolution.pokedexId} className="text-center">
                <p>{evolution.name}</p>
              </div>
            ))}
          </div>
        ) : (
          <p className="text-center">Aucune évolution disponible</p>
        )}
      </div>
    </div>
  );
};

export default PokemonDetails;
